<template>
  <div class="rank" ref="rank">
    <app-scroll class="toplist" ref="toplist">
      <ul>
        <li @click="selectItem(item)" class="item" v-for="item in topList">
          <div class="icon">
            <img width="100" height="100" v-lazy="item.picUrl" />
          </div>
          <ul class="songlist">
            <li class="song" v-for="(song,index) in item.songList">
              <span>{{index + 1}}</span>
              <span>{{song.songname}}-{{song.singername}}</span>
            </li>
          </ul>
        </li>
      </ul>
      <div class="loading-container" v-show="!topList.length">
        <app-loading></app-loading>
      </div>
    </app-scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
import AppScroll from 'base/Scroll/Scroll'
import AppLoading from 'base/Loading/Loading'
import { getTopList } from 'api/rank'
import { CODE_OK } from 'api/config'
import PlaylistMixin from '@/mixins/PlaylistMixin'
import { mapActions } from 'vuex'
import { ACTION_TYPES as COMMON_ACTION_TYPES } from '@/store/common/actions'


export default {
  mixins: [PlaylistMixin],
  created() {
    this._getTopList()
  },
  data() {
    return {
      topList: []
    }
  },
  methods: {
    ...mapActions({ ...COMMON_ACTION_TYPES }),
    selectItem(item) {
      this.$router.push({
        path: `/rank/${item.id}`
      })
      this.setTopList(item)
    },
    handlePlaylist(playlist) {
      const bottom = playlist.length > 0 ? '60px' : ''

      this.$refs.rank.style.bottom = bottom
      this.$refs.toplist.refresh()
    },
    _getTopList() {
      getTopList().then((res) => {
        if (res.code === CODE_OK) {
          this.topList = res.data.topList
          // console.log('topList', this.topList)
        }
      })
    }

  },
  watch: {
    topList() {

    }
  },
  components: {
    AppScroll,
    AppLoading

  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';

.rank {
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;

  .toplist {
    height: 100%;
    overflow: hidden;

    .item {
      display: flex;
      margin: 0 20px;
      padding-top: 20px;
      height: 100px;

      &:last-child {
        padding-bottom: 20px;
      }

      .icon {
        flex: 0 0 100px;
        width: 100px;
        height: 100px;
      }

      .songlist {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 20px;
        height: 100px;
        overflow: hidden;
        background: $color-highlight-background;
        color: $color-text-d;
        font-size: $font-size-small;

        .song {
          no-wrap();
          line-height: 26px;
        }
      }
    }

    .loading-container {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>


